import React , {useEffect , useState} from "react";
import  "react-bootstrap";
import '../App.css'
import { Component } from 'react';

class ADD extends Component{

   
 
  
  render() {

  function showAttributs(e) {
    var x = document.getElementById("Book");
    var y = document.getElementById("DVD");
    var z = document.getElementById("Furniture");
    if (e.target.value === "Book") {
      x.style.display = "block";
      y.style.display = "none";
      z.style.display = "none";
    }
   else if (e.target.value === "DVD") {
      y.style.display = "block";
      x.style.display = "none";
      z.style.display = "none";
    }
    else if (e.target.value === "Furniture") {

      z.style.display = "block";
      x.style.display = "none";
      y.style.display = "none";
    }
    else {
      z.style.display = "none";
      y.style.display = "none";
      x.style.display = "none";
    }
  }
        return(

          
        <><div className="container-fluid">
          <section className="title">
            <nav class="navbar navbar-expand-lg navbar-light ">
              <button class="navbar-toggler ms-auto order-0" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
              </button>
              <div class="container-fluid">
                <h2>Product Add</h2>
                <div class="collapse navbar-collapse " id="navbarSupportedContent">
                  <ul class="navbar-nav ms-auto order-0 ">
                    <li class="nav-item">
                      <button type="button" class="btn btn-success" id='save-product-btn'>Save</button>
                    </li>
                    <li class="nav-item">
                    <a href="/" class="btn btn-danger" role="button" id='cancel-add-product-btn' aria-pressed="true">Cancel</a>
                    </li>
                  </ul>
                </div>
              </div>
            </nav>
            <hr className='hr-navbar'></hr>
          </section> 

          <section className="product-add-form-section">
          <div className="container-fluid">
          <form id="product-form">
  <div class="form-group row">
    <label for="SKU" class="col-lg-1
     col-form-label" >SKU</label>
    <div class="col-lg-2">
      <input type="text" readonly class="form-control-plaintext" id="sku" placeholder="SKU" className="form-control"/>
    </div>
  </div>
  <div class="form-group row">
    <label for="SKU" class="col-lg-1
     col-form-label" >Name</label>
    <div class="col-lg-2">
      <input type="text" readonly class="form-control-plaintext" id="name" placeholder="Name" className="form-control"/>
    </div>
  </div>
  <div class="form-group row">
    <label for="SKU" class="col-lg-1
     col-form-label" >Price ($)</label>
    <div class="col-lg-2">
      <input type="number" readonly class="form-control-plaintext" id="price" placeholder="Price" className="form-control"></input>
    </div>
  </div>
  <div class="form-group row">
    <label for="SKU" class="col-lg-2
     col-form-label" >Type Switcher</label>
    <div class="col-lg-6">
    <select class="custom-select col-lg-6 "  id="product-type" onChange={showAttributs} >
    <option selected disabled>Choose...</option>
    <option value="DVD" >DVD </option>
    <option value="Book">Book</option>
    <option value="Furniture">Furniture</option>
  </select>
  <div className="container-fluid" id="switch-forms">
  <div id="DVD">
      <div class="form-group row" >
      <label  class="col-lg-2 col-form-label" >Size (MB)</label>
       <div class="col-lg-8">
      <input type="number" min="0" readonly class="form-control-plaintext" id="size" placeholder="size" className="form-control"></input>
      <p><p className="star">*</p> please provide the size of the disc in MB</p>
     </div>
      </div>
    </div>
    <div id="Book">
      <div class="form-group row" >
      <label  class="col-lg-2 col-form-label" >Weight (kg)</label>
       <div class="col-lg-8">
      <input type="number" min="0" readonly class="form-control-plaintext" id="size" placeholder="Weight" className="form-control"></input>
      <p><p className="star">*</p> please provide the weight of the book in KG</p>
     </div>
      </div>
    </div>
  <div id="Furniture">
      <div class="form-group row" >
      <label class="col-lg-12 col-form-label" >Dimensions (H*W*L)</label>
       <div class="col-lg-4">
      <input type="number" min="0" readonly class="form-control-plaintext" id="size" placeholder="Height" className="form-control"></input>
     </div>
     <div class="col-lg-4">
      <input type="number" min="0" readonly class="form-control-plaintext" id="size" placeholder="Width" className="form-control"></input>
     </div>
     <div class="col-lg-4">
      <input type="number" min="0" readonly class="form-control-plaintext" id="size" placeholder="Length" className="form-control"></input>
     </div>
     <p><p className="star">*</p> please provide the dimensions of the furniture in H*W*L format</p>
      </div>
    </div>
  </div>
  </div>
  </div>
</form>


</div>

          </section>
          <hr className='hr-navbar'></hr>
          <footer class="text-muted" id="footer-add">
      <div class="container-fluid float-right">
        <p>© Copyright Scandiweb</p>
      </div>
          </footer>
            </div>
            </>
            
          
       )

     
    
    }
    
  }



export default ADD;